import { NgModule } from '@angular/core';
import { RouterModule,Routes } from '@angular/router';

import { MatButtonModule } from '@angular/material/button';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';

import { MatChipsModule } from '@angular/material/chips';
import { MatRippleModule } from '@angular/material/core';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatSortModule } from '@angular/material/sort';
import { MatTableModule } from '@angular/material/table';
import { MatTabsModule } from '@angular/material/tabs';
import { MatRadioModule } from '@angular/material/radio';

import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatListModule } from '@angular/material/list';

import { NgxChartsModule } from '@swimlane/ngx-charts';
import { AgmCoreModule } from '@agm/core';
import { FuseWidgetModule } from '@fuse/components/widget/widget.module';

import { MatStepperModule } from '@angular/material/stepper';


import { FuseSharedModule } from '@fuse/shared.module';

import { EncuestasComponent } from 'app/main/apps/encuestas/encuestas/encuestas.component';
import { EncuestaComponent } from 'app/main/apps/encuestas/encuesta/encuesta.component';
import { EncuestaResultadoComponent } from 'app/main/apps/encuestas/resultado/resultado.component';
import { EncuestasResultadosComponent } from 'app/main/apps/encuestas/resultados/resultados.component';
import { EncuestasService } from 'app/main/apps/encuestas/encuestas.service';
import { EncuestaService } from 'app/main/apps/encuestas/encuesta.service';
import { EncuestaResultadoService } from 'app/main/apps/encuestas/resultado/resultado.service';
import { EncuestaResultadosService } from 'app/main/apps/encuestas/resultados/resultados.service';
import { FuseSidebarModule } from '@fuse/components';

import { EncuestasFormularioComponent } from 'app/main/apps/encuestas/formulario/formulario.component'
import { EncuestaFormulaioService } from 'app/main/apps/encuestas/formulario/formulario.services'
const routes = [
   
    {
        path     : 'formulario',
        component: EncuestasFormularioComponent,
        resolve  : {
            data: EncuestaFormulaioService
        }
    },{
        path     : 'formulario/:idEncuesta/:idFormulario',
        component: EncuestasFormularioComponent,
        resolve  : {
            data: EncuestaFormulaioService
        }
    }, {
        path     : 'formulario/:idEncuesta/:idFormulario/:idCuestionario',
        component: EncuestasFormularioComponent,
        resolve  : {
            data: EncuestaFormulaioService
        }
    },
    {
        path     : 'resultado/:id',
        component: EncuestaResultadoComponent,
        resolve  : {
            data: EncuestaResultadoService
        }
    }, 
    {
        path     : 'resultados',
        component: EncuestasResultadosComponent,
        resolve  : {
            data: EncuestaResultadosService
        }
    },
    {
        path     : 'resultados/:idEncuesta',
        component: EncuestasResultadosComponent,
        resolve  : {
            data: EncuestaResultadosService
        }
    },
    {
        path     : 'encuestas',
        component: EncuestasComponent,
        resolve  : {
            academy: EncuestasService
        }
    },
    {
        path     : 'encuestas/:courseId/:courseSlug',
        component: EncuestaComponent,
        resolve  : {
            academy: EncuestaService
        }
    },
    {
        path      : '**',
        redirectTo: 'encuestas'
    }
];

@NgModule({
    declarations: [
        EncuestasComponent,
        EncuestaComponent,
        EncuestaResultadoComponent,
        EncuestasResultadosComponent,
        EncuestasFormularioComponent 
    ],
    imports     : [
        RouterModule.forChild(routes),

        MatButtonModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatSelectModule,
        MatChipsModule,
        MatRippleModule,
        MatExpansionModule,
        MatPaginatorModule,
        MatSnackBarModule,
        MatSortModule,
        MatTableModule,
        MatTabsModule,
        MatRadioModule,
        MatButtonToggleModule,MatListModule,
        NgxChartsModule,
        AgmCoreModule,
        FuseWidgetModule,
        MatStepperModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyD81ecsCj4yYpcXSLFcYU97PvRsE_X8Bx8'
        }),
    
    
    
        FuseSharedModule,
        FuseSidebarModule
    ],exports: [
        MatButtonModule,
        MatFormFieldModule,
        MatInputModule,
        MatRippleModule,
        MatRadioModule,
        MatButtonToggleModule,MatListModule
      ],
    providers   : [
        EncuestasService,
        EncuestaService,
        EncuestaResultadoService,
        EncuestaResultadosService,
        EncuestaFormulaioService
    ],
    
  schemas: [ MatRadioModule, MatButtonToggleModule,MatListModule ]
})
export class EncuestaModule
{
}
