import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';

import { takeUntil } from 'rxjs/operators';
import { JmyService } from 'app/concomsis/jmy/jmy.service'


export class PasosInterface {
    id:string
    title:string
    content:string
    idFormulario:string

    constructor(actual:any){
        this.id = actual.id || '';
        this.title  = actual.title || '';
        this.content  = actual.content || '';
        this.idFormulario   = actual.idFormulario || '';
    }
}

export class EncuestaInterface {
    id:string
    idFormulario:string
    title:string
    slug:string
    description:string
    category:string
    length?:string
    totalSteps?:string
    updated?:string
    steps?:PasosInterface[]

    constructor(actual:any){
        this.id = actual.id || '';
        this.idFormulario = actual.idFormulario || '';
        this.title  = actual.title || '';
        this.slug   = actual.slug || '';
        this.description    = actual.description || '';
        this.category   = actual.category || '';
        this.length = actual.length || '';
        this.updated    = actual.updated || '';        
        this.steps  = actual.steps || [];
        this.totalSteps = String(this.steps.length);
    }
}

@Injectable()
export class EncuestaService implements Resolve<any>
{
    onCourseChanged: BehaviorSubject<any>;

    encuesta:EncuestaInterface;
    private _unsubscribeAll: Subject<any>;
    private _unperfilChange: any;
    private _unencuestaChange: any;
    private _unpasosChange: any;
    /**
     * Constructor
     *
     * @param {HttpClient} _httpClient
     */
    constructor(
        private _httpClient: HttpClient,
        private jmyService: JmyService
    )
    {
        // Set the defaults
        this.onCourseChanged = new BehaviorSubject({});
       // this.encuesta = new EncuestaInterface({});
        //console.log(this.encuesta);
        
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Resolver
     *
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        return new Promise((resolve, reject) => {

            Promise.all([
                this.getCourse(route.params.courseId, route.params.courseSlug)
            ]).then(
                () => {
                    resolve();
                },
                reject
            );
        });
    }

    /**
     * Get course
     *
     * @param courseId
     * @param courseSlug
     * @returns {Promise<any>}
     */
    getCourse(courseId, courseSlug): Promise<any>
    {
     return new Promise((resolve, reject) => {


       this._unperfilChange= this.jmyService.jmyPerfil.perfilChange.subscribe(perfilUsuario=>{

        if(perfilUsuario.uid!=''){
            this._unencuestaChange=this.jmyService.dbRealTime.object('/encuestas/'+ perfilUsuario.eid +'/encuestas/'+courseId).valueChanges().subscribe(res=>{
                this._unencuestaChange.unsubscribe();
                this.encuesta = new EncuestaInterface(res);
                console.log(res);
                this.onCourseChanged.next(this.encuesta);
            })
            this._unpasosChange = this.jmyService.dbRealTime.object('/encuestas/'+ perfilUsuario.eid +'/pasos/'+courseId).valueChanges().subscribe(rP=>{
                this._unpasosChange.unsubscribe();
                const resPasos:any = rP;
                this.encuesta.steps = resPasos.map(m=>{                            
                    return new PasosInterface(m);
                });  
                this.encuesta.totalSteps = String(this.encuesta.steps.length)
                this.onCourseChanged.next(this.encuesta);
                console.log( this.encuesta);
                resolve( this.encuesta)
            })    
        }

           

    })


/*
            this._httpClient.get('api/academy-course/' + courseId + '/' + courseSlug)
                .subscribe((response: any) => {
                    this.onCourseChanged.next(response);
                    resolve(response);
                }, reject);*/
        });
    }


    ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        //this._ecommerceProductService.jmyService.jmyFn.configuracionArchivos.complete();
        //this._ecommerceProductService.jmyService.jmyFn.botonEnEsperaArchivos.complete();
        //  this._ecommerceProductService.jmyService.jmyFn.treeArchivos.complete();
        this._unperfilChange.unsubscribe();
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

}



