import { FuseUtils } from '@fuse/utils';
import { infoPerfilInterface} from 'app/concomsis/jmy/interfaces/services';

export  class EncuestaFormularioOpcionesInterface {
    nombre:string
    valor:string
}

export  class EncuestaIndexInterface {
    id: string;
    reference: string;
    subtotal: string;
    tax: string;
    discount: string;
    total: string;
    customer: any;
    products: any[];
    payment: any;
    shippingDetails: any[];

    idFormulario:string
    idEncuesta:string
    idCuestionario:string
    date:string
    encuestador:infoPerfilInterface
    status:{
        id:string
        name:string
        color:string
        date:string
        nombre:string
    }[]
    preguntas:{
        cantidad:string
        respuestas:string
    }
    respuestas:EncuestaFormularioInterface[]
    constructor(d:any){
        if(d==undefined)
            d={}

        this.id = d.id || FuseUtils.generateGUID();
        this.reference = d.reference || FuseUtils.generateGUID();
        this.subtotal = d.subtotal || 0;
        this.tax = d.tax || 0;
        this.discount = d.discount || 0;
        this.total = d.total || 0;
        this.date = d.date || '';
        this.customer = d.customer || {};
        this.products = d.products || [];
        this.payment = d.payment || {};
        this.shippingDetails = d.shippingDetails || [];
        this.idFormulario= d.idFormulario||'';
        this.idEncuesta= d.idEncuesta||'';
        this.idCuestionario= d.idCuestionario||'';
        this.encuestador= new infoPerfilInterface(d.encuestador||{});
        this.date= d.date||'';
        this.status=d.status||[];
        this.preguntas=d.preguntas||{cantidad:'0',respuestas:'0'};
        this.respuestas=d.respuestas||{cantidad:'0',respuestas:'0'};
    }
} 
export  class EncuestaFormularioInterface {
    comentario:string
    id:string
    multiple:string
    titulo:string
    pregunta:string
    respuestas:object   []
    constructor(formulario:any){
        this.comentario = formulario.comentario || '';
        this.id = formulario.id || '';
        this.multiple = formulario.multiple || 'false';
        this.titulo = formulario.titulo || '';
        this.pregunta = formulario.pregunta || '';
        this.respuestas = formulario.respuestas || [];
    }
} 

export class EncuestasFormularioResultadosInterface{
    id:string
    uid:string
    idEncuesta:string
    idPregunta:string
    respuestas:string[]
    timestamp:string
    time:string
    editable:string
    constructor(d){    
        this.id = d.id || FuseUtils.generateGUID();
        this.uid = d.uid || '';
        this.idEncuesta = d.idEncuesta || '';
        this.idPregunta = d.idPregunta || '';
        this.respuestas = d.respuestas || [];
        this.timestamp = d.timestamp ||'';
        this.time = d.time || '';
        this.editable = d.editable || 'false';
    }
}