import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { BehaviorSubject, Observable, from } from 'rxjs';
import { JmyService} from 'app/concomsis/jmy/jmy.service';
import { infoPerfilInterface} from 'app/concomsis/jmy/interfaces/services';
import {EncuestaFormularioOpcionesInterface,EncuestaFormularioInterface, EncuestaIndexInterface }  from 'app/main/apps/encuestas/interfaces';
import { FuseUtils } from '@fuse/utils';
@Injectable()
export class EncuestaFormulaioService implements Resolve<any>
{
    routeParams: any;
    preguntas: EncuestaFormularioInterface[];
    onPreguntasChanged: BehaviorSubject<{preguntas:EncuestaFormularioInterface[]}>;
    idCuestionario:string;
    idEncuesta:string;
    idFormulario:string;
    /**
     * Constructor
     *
     * @param {HttpClient} _httpClient
     */
    constructor(
       public jmyService:JmyService
    )
    {
        // Set the defaults
        this.onPreguntasChanged = new BehaviorSubject({preguntas:[]});
    }

    /**
     * Resolver
     *
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        this.routeParams = route.params;
        this.idCuestionario=this.routeParams.idCuestionario||FuseUtils.generateGUID();
        this.idFormulario=this.routeParams.idFormulario||'';
        this.idEncuesta=this.routeParams.idEncuesta||'';

        console.log(this.routeParams,this.idCuestionario);
        

        return new Promise((resolve, reject) => {

            Promise.all([
                this.obtenerPreguntas()
            ]).then(
                () => {
                    resolve();
                },
                reject
            );
        });
    }

    /**
     * Get order
     *
     * @returns {Promise<any>}
     */




    guardarPreguntas(respuestas): Promise<boolean>
    {
        return new Promise((resolve, reject) => {

            console.log(respuestas,new infoPerfilInterface(this.jmyService.jmyPerfil.perfil));
            
            if(this.jmyService.jmyPerfil.perfil.uid!='')
               
                this.jmyService.dbRealTime.object('/encuestas/'+this.jmyService.jmyPerfil.perfil.eid+'/respuestasIndex/'+this.idCuestionario).set(
                    new EncuestaIndexInterface({                  
                    idFormulario:this.idFormulario,
                    idEncuesta:this.idEncuesta,
                    idCuestionario:this.idCuestionario,
                    date:this.jmyService.jmyFn.fnT.tiempo().slice(0,-6),
                    status:[{
                        id:(this.preguntas.length==Object.keys(respuestas).length)?2:1,
                        name:(this.preguntas.length==Object.keys(respuestas).length)?'Sin confirmar':'Sin acabar',
                        color:(this.preguntas.length==Object.keys(respuestas).length)?'purple-300':'blue-500',
                        date:this.jmyService.jmyFn.fnT.tiempo().slice(0,-6),
                        nombre:(this.preguntas.length==Object.keys(respuestas).length)?'Sin confirmar':'Sin acabar',                        
                    }],
                    encuestador:new infoPerfilInterface(this.jmyService.jmyPerfil.perfil),
                    preguntas:{
                        cantidad:this.preguntas.length,
                        respuestas:Object.keys(respuestas).length,
                    }
                })).then(r=>{
                    this.jmyService.dbRealTime.object('/encuestas/'+this.jmyService.jmyPerfil.perfil.eid+'/respuestas/'+this.idCuestionario).set({
                        respuestas:respuestas,
                        preguntas:this.preguntas,
                    }).then(r=>{
                        console.log(r);
                        resolve(true);
                    })
                })
            
        });
    }

     

    obtenerPreguntas(): Promise<{preguntas:EncuestaFormularioInterface[]}>{
        return new Promise((resolve, reject) => {
            if(this.jmyService.jmyPerfil.perfil.uid!='')
                this.jmyService.dbRealTime.
                    object('/encuestas/'+this.jmyService.jmyPerfil.perfil.eid+'/formularios/'+this.idFormulario)
                    .valueChanges().subscribe(r=>{



                  /*      this.jmyService.dbRealTime.
                        object('/encuestas/'+this.jmyService.jmyPerfil.perfil.eid+'/respuestas/'+this.idCuestionario)
                        .valueChanges().subscribe(respuestas=>{
                            console.log(respuestas);
                            

                        });*/



                       console.log(r, this.preguntas);
                       this.preguntas = Object.keys(r['preguntas']).map(m=>{ return new EncuestaFormularioInterface(r['preguntas'][m])});
                       this.onPreguntasChanged.next({preguntas:this.preguntas})
                       resolve( {preguntas:this.preguntas})
                })
            
        });
    }

}
