import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { JmyService }from 'app/concomsis/jmy/jmy.service'
import { EncuestaIndexInterface, EncuestaFormularioInterface }  from 'app/main/apps/encuestas/interfaces';
import { infoPerfilInterface} from 'app/concomsis/jmy/interfaces/services';

@Injectable()
export class EncuestaResultadoService implements Resolve<any>
{
    routeParams: any;
    order: {index:EncuestaIndexInterface,res:any};
    onOrderChanged: BehaviorSubject<{index:EncuestaIndexInterface,res:any}>;
    index:any;
    /**
     * Constructor
     *
     * @param {HttpClient} _httpClient
     */
    constructor(
        private _httpClient: HttpClient,
        private jmyService: JmyService
    )
    {
        // Set the defaults
                     
        this.order =  {index:new EncuestaIndexInterface({}),res:[]};
        this.onOrderChanged = new BehaviorSubject(this.order);
    }

    /**
     * Resolver
     *
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        this.routeParams = route.params;

        return new Promise((resolve, reject) => {

            Promise.all([
                this.getOrder()
            ]).then(
                () => {
                    resolve();
                },
                reject
            );
        });
    }

    /**
     * Get order
     *
     * @returns {Promise<any>}
     */
    getOrder(): Promise<{index:EncuestaIndexInterface,res:any}>
    {
        return new Promise((resolve, reject) => {
            this.jmyService.jmyPerfil.perfilChange.subscribe(perfil=>{
                this.jmyService.dbRealTime.object('/encuestas/'+perfil.eid+'/respuestasIndex/'+ this.routeParams.id).valueChanges().subscribe(rI=>{
                    //console.log('index',rI); 
                    this.index=rI;
                    this.jmyService.dbRealTime.object('/encuestas/'+perfil.eid+'/respuestas/'+ this.routeParams.id).valueChanges().subscribe(r=>{
                        if(r!=undefined){
                            this.order =  {index:new EncuestaIndexInterface(this.index),res:r};
                            this.onOrderChanged.next(this.order);
                            console.log(r,this.order);
                          //  console.log(this.order);
                            resolve(this.order);
                        }
                    });
                });
       
        });
        });
    }


    /**
     * Save order
     *
     * @param order
     * @returns {Promise<any>}
     */
    saveOrder(order): Promise<any>
    {
        return new Promise((resolve, reject) => {
            this._httpClient.post('api/e-commerce-orders/' + order.id, order)
                .subscribe((response: any) => {
                    resolve(response);
                }, reject);
        });
    }

    /**
     * Add order
     *
     * @param order
     * @returns {Promise<any>}
     */
    addOrder(order): Promise<any>
    {
        return new Promise((resolve, reject) => {
            this._httpClient.post('api/e-commerce-orders/', order)
                .subscribe((response: any) => {
                    resolve(response);
                }, reject);
        });
    }
}
