import { Component, OnDestroy, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { MatSelectionList } from '@angular/material';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject, BehaviorSubject } from 'rxjs';

import {EncuestaFormularioInterface, EncuestasFormularioResultadosInterface}  from 'app/main/apps/encuestas/interfaces';
import { infoPerfilInterface} from 'app/concomsis/jmy/interfaces/services';
import {EncuestaFormulaioService}  from 'app/main/apps/encuestas/formulario/formulario.services';
import {  Router } from '@angular/router';
import * as $ from 'jquery';
import { ToastrService } from 'ngx-toastr'
@Component({
    selector   : 'encuesta-formulario',
    templateUrl: './formulario.component.html',
    styleUrls  : ['./formulario.component.scss']
})
export class EncuestasFormularioComponent implements OnInit, OnDestroy
{
    
    form: FormGroup;

    // Horizontal Stepper
    horizontalStepperStep1: FormGroup;
    pasos:EncuestaFormularioInterface[];
    pasosCambio:BehaviorSubject<EncuestaFormularioInterface[]>;
    div:{
        comentarios:boolean
        codigo:boolean
        regresar:boolean
    };
    tituloFormulario:string='';
    descripcionFormulario:string='';
    comentario:string;
    idCuestionario:string;
    selectedOptions:any;
    
    repuestas:object;
    perfilChange:any;
    telefonoConfirmar:string;
    // Private

    private _unsubscribeAll: Subject<any>;

    /**
     * Constructor
     *
     * @param {FormBuilder} _formBuilder
     */
    
    constructor(
        private _formBuilder: FormBuilder,
        private toast: ToastrService,
        private _router: Router,
        private _encuestaFormulaioService: EncuestaFormulaioService
        
    )
    {
        // Set the private defaults
        this._unsubscribeAll = new Subject();
        this.pasos=[];
        this.pasosCambio = new BehaviorSubject(this.pasos);
        this.div={
            comentarios:false,
            codigo:false,
            regresar:false,
        };
        this.repuestas = {};
        this.telefonoConfirmar='';
        this.comentario='';
        this.idCuestionario='';
    /*   this.pasosCambio.subscribe(r=>{
            console.log(r);
            if(this._encuestaFormulaioService.idCuestionario!='')
                this._encuestaFormulaioService.guardarPreguntas(this.repuestas).then(rG=>{
                    console.log(rG);
                    
                })
        });*/
    }
    confirmarEncuesta(){
        this.toast.success('Encuesta terminada correctamente con el F√≥lio '+this.idCuestionario,'Encuesta terminada ');
        this.div.regresar=true;
    }
    verificarNumero(d:any){
        console.log(d,d.id,$('#'+d.id).val());
        if($('#'+d.id).val().length>9){
            this.div.codigo=true;
        }else
            this.toast.error('Ingrese un n√∫mero telef√≥nico a 10 digitos','N√∫mero no valido');
    }
    onNgModelChange(idPregunta:string,val:string){
        console.log(idPregunta,val);
        let fn={
            agregarQuitar(respuestas, target){
                console.log(respuestas, target);
                if(!respuestas.includes(target))
                    respuestas.push(target);
                else
                    respuestas = respuestas.filter(f=>{
                        return f!=target
                    })  
                return respuestas
            },
        };
        
        this.perfilChange = this._encuestaFormulaioService.jmyService.jmyPerfil.perfilChange.subscribe(perfil=>{
            if(this.repuestas[idPregunta]==undefined)
                this.repuestas[idPregunta]={};
            this.repuestas[idPregunta] = new EncuestasFormularioResultadosInterface({
                idEncuesta:this._encuestaFormulaioService.idEncuesta,
                idFormulario:this._encuestaFormulaioService.idFormulario,
                idPregunta:idPregunta,
                respuestas:(this.repuestas[idPregunta].respuestas==undefined)?[val]:fn.agregarQuitar(this.repuestas[idPregunta].respuestas,val),
                time:String(Date.now()),
                timestamp:this._encuestaFormulaioService.jmyService.jmyFn.funcionesTiempo.tiempo().slice(0,-6),
                uid:new infoPerfilInterface(this._encuestaFormulaioService.jmyService.jmyPerfil.perfil)
            })
            this.verificarPreguntas(idPregunta).then(r=>{
                console.log(r);
                
            }); 
            //if(this._encuestaFormulaioService.idCuestionario!='')
            this._encuestaFormulaioService.guardarPreguntas(this.repuestas).then(rG=>{
                this.idCuestionario = this._encuestaFormulaioService.idCuestionario;
                console.log(rG);
                })
            console.log(this.repuestas,this.idCuestionario );
        })
    }
    verificarPreguntas(idPregunta:string=''){
        return new Promise((res,rej)=>{
            let t =new EncuestaFormularioInterface(this.pasos.find(f=>{return Object.keys(this.repuestas).includes(f.id) })||{});

            if(t.id!='')
                if(t.multiple!='true'){
                    let o =[];
                    this.pasos.forEach(r=>{
                        if(t.id!=r.id)
                            o.push(r);
                    })
                    this.pasos=o;
                    this.pasosCambio.next(this.pasos);
                    console.log(t);
                }
            
        })
    }
    comentariosToggle(){
        this.div.comentarios=(this.div.comentarios)?false:true;
        this.comentario=''; 
    }
    // -----------------------------------------------------------------------------------------------------
    // @ Lifecycle hooks
    // -----------------------------------------------------------------------------------------------------

    /**
     * On init
     */
    ngOnInit(): void
    {
       
        this._encuestaFormulaioService.onPreguntasChanged.subscribe(r=>{
            console.log('this.pasos',r);
            this.pasos=r.preguntas.map(m=>{return new EncuestaFormularioInterface(m)});
            this.pasosCambio.next(this.pasos);
            this.verificarPreguntas().then(r=>{
                console.log(r);
            });
        })
       
    }

    /**
     * On destroy
     */
    ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
      //  this.pasosCambio.complete();
        //this.perfilChange.unsubscribe();
     //  this._encuestaFormulaioService.onPreguntasChanged.unsubscribe();
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Finish the horizontal stepper
     */
    finishHorizontalStepper(): void
    {
        this._router.navigate(['/apps/encuestas/encuestas']);
       
    }

    /**
     * Finish the vertical stepper
     */
  
}
