import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { JmyService } from 'app/concomsis/jmy/jmy.service';

@Injectable()
export class EncuestasService implements Resolve<any>
{
    onCategoriesChanged: BehaviorSubject<any>;
    onCoursesChanged: BehaviorSubject<any>;

    /**
     * Constructor
     *
     * @param {HttpClient} _httpClient
     */
    constructor(
        private _httpClient: HttpClient,
        private jmyService: JmyService
    )
    {
        // Set the defaults
        this.onCategoriesChanged = new BehaviorSubject({});
        this.onCoursesChanged = new BehaviorSubject({});
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Resolver
     *
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        return new Promise((resolve, reject) => {

            Promise.all([
                this.getCategories(),
                this.getCourses()
            ]).then(
                () => {
                    resolve();
                },
                reject
            );
        });
    }

    /**
     * Get categories
     *
     * @returns {Promise<any>}
     */
    getCategories(): Promise<any>
    {
        return new Promise((resolve, reject) => {
            this.jmyService.jmyPerfil.perfilChange.subscribe(r=>{
                if(r!=undefined)
                    if(r.uid!=''){
                        const rq:string='/encuestas/'+r.eid+'/categorias'
                        const q = this.jmyService.dbRealTime.list(rq).snapshotChanges().subscribe(rB=>{
                            console.log(rB);
                            const qR:any[]=rB.map(c => ({ key: c.payload.key, ...c.payload.val() }));
                            console.log(qR);
                            
                            //res(qR);
                            this.onCategoriesChanged.next(qR);
                            resolve(qR);
                        });
                    }
            }) 
           /* this._httpClient.get('api/academy-categories')
                .subscribe((response: any) => {
                    this.onCategoriesChanged.next(response);
                    resolve(response);
                }, reject);*/
        });
    }

    /**
     * Get courses
     *
     * @returns {Promise<any>}
     */
    getCourses(): Promise<any>
    {
        return new Promise((resolve, reject) => {
            this.jmyService.jmyPerfil.perfilChange.subscribe(r=>{
                if(r!=undefined)
                    if(r.uid!=''){
                        const rq:string='/encuestas/'+r.eid+'/encuestas'
                        const q = this.jmyService.dbRealTime.list(rq).snapshotChanges().subscribe(rB=>{
                            console.log(rB);
                            const qR:any[]=rB.map(c => ({ key: c.payload.key, ...c.payload.val() }));
                            console.log(qR);
                            
                            //res(qR);
                            this.onCoursesChanged.next(qR);
                            resolve(qR);
                        });
                    }
            })
        });
    }

}
