import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { JmyService } from 'app/concomsis/jmy/jmy.service';
import { EncuestaIndexInterface }  from 'app/main/apps/encuestas/interfaces';
@Injectable()
export class EncuestaResultadosService implements Resolve<any>
{
    orders: any[];
    onOrdersChanged: BehaviorSubject<any>;
    routeParams:any;
    /**
     * Constructor
     *
     * @param {HttpClient} _httpClient
     */
    constructor(
        private _httpClient: HttpClient,
        private jmyService: JmyService
    )
    {
        // Set the defaults
        this.onOrdersChanged = new BehaviorSubject({});
    }

    /**
     * Resolver
     *
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {   
        this.routeParams=route.params;
        return new Promise((resolve, reject) => {

            Promise.all([
                this.getOrders()
            ]).then(
                () => {
                    resolve();
                },
                reject
            );
        });
    }

    /**
     * Get orders
     *
     * @returns {Promise<any>}
     */
    getOrders(): Promise<EncuestaIndexInterface[]>
    {
        return new Promise((resolve, reject) => {
            console.log(this.routeParams);
            
            this.jmyService.jmyPerfil.perfilChange.subscribe(perfil=>{
                this.jmyService.dbRealTime.list('/encuestas/'+perfil.eid+'/respuestasIndex').valueChanges().subscribe(r=>{
                    console.log(r);
                    this.orders = r.map(m=>{return new EncuestaIndexInterface(m)});
                    this.onOrdersChanged.next(this.orders);
                    resolve(this.orders);
                });
               /* this._httpClient.get('api/e-commerce-orders')
                    .subscribe((response: any) => {
                        this.orders = response;
                        this.onOrdersChanged.next(this.orders);
                        resolve(response);
                    }, reject);*/
            });
        })
    }
}
