import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subject} from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { fuseAnimations } from '@fuse/animations';
import { EncuestaIndexInterface }  from 'app/main/apps/encuestas/interfaces';
import { orderStatuses } from 'app/main/apps/encuestas/resultado/resultado-statuses';
import { Resultado } from 'app/main/apps/encuestas/resultado/resultado.model';
import { EncuestaResultadoService } from 'app/main/apps/encuestas/resultado/resultado.service';

@Component({
    selector     : 'encuestas-resultado',
    templateUrl  : './resultado.component.html',
    styleUrls    : ['./resultado.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class EncuestaResultadoComponent implements OnInit, OnDestroy
{
    order: EncuestaIndexInterface;
    orderStatuses: any;
    statusForm: FormGroup;
    respuestas:object[];

    // Private
    private _unsubscribeAll: Subject<any>;

    /**
     * Constructor
     *
     * @param {EncuestaResultadoService} _ecommerceOrderService
     * @param {FormBuilder} _formBuilder
     */
    constructor(
        private _ecommerceOrderService: EncuestaResultadoService,
        private _formBuilder: FormBuilder
    )
    {
        // Set the defaults
        this.order = new EncuestaIndexInterface({});
        this.orderStatuses = orderStatuses;
        this.respuestas=[];
        // Set the private defaults
        this._unsubscribeAll = new Subject();
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Lifecycle hooks
    // -----------------------------------------------------------------------------------------------------

    /**
     * On init
     */
    ngOnInit(): void
    {
        // Subscribe to update order on changes
        this._ecommerceOrderService.onOrderChanged
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(order => {
                this.order = new EncuestaIndexInterface(order.index );
                this.respuestas=Object.keys(order.res.respuestas).map(m=>{
                    return {
                        ...order.res.respuestas[m],
                        respuestasString:order.res.respuestas[m].respuestas.join(', '),
                        preguntaFinal: order.res['preguntas'].find(f=>{
                            return  order.res.respuestas[m].idPregunta === f.id
                        })
                    }});
                console.log(order);
                console.log(this.respuestas);

            });

        this.statusForm = this._formBuilder.group({
            newStatus: ['']
        });
    }

    /**
     * On destroy
     */
    ngOnDestroy(): void {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Update status
     */
    updateStatus(): void
    {
        const newStatusId = Number.parseInt(this.statusForm.get('newStatus').value);

        if ( !newStatusId )
        {
            return;
        }

        const newStatus = this.orderStatuses.find((status) => {
            return status.id === newStatusId;
        });

        newStatus['date'] = new Date().toString();

        this.order.status.unshift(newStatus);
    }
}
